.keypad{
    position: relative;
    display: grid;
    grid-gap: 10px;
    grid-template-columns: repeat(4, 1fr);
    padding: 10px;
    background-color: rgb(35, 68, 46);
    border-radius: 0px 0px 30px 30px;
}

button {
    font-size: 20px;
    font-weight: 100;
    border: none; 
    /* border-radius:50%; */
    background: rgba(34, 54, 69, 0.964);;
    cursor: pointer;
}
.span-2 {
    grid-column: span 2;
}
.span{
    color: rgb(255, 251, 254);
    grid-row: span 2;
    font-size: 2em;
}

.btn {
    color: rgb(255, 251, 254);
    font-size: 2em;
    min-height: 65px;
}

.clear {
    background-color: rgb(157, 34, 54);
}
.btn:hover {
    background: rgb(185, 145, 145);
}
.btn:active {
    background: rgb(61, 27, 27);
}
.orange {
   background-color: rgb(231, 104, 40);
}
.green{
  background-color: rgba(3, 144, 210, 0.94);
}
.back{
    /* height: 1em; */
    width: 1em;
    color: rgb(21, 197, 59);
    background-color:black;
    position: absolute;
    /* padding: 3px; */
    /* left: 20px; */
    top: -30px;
    right: 10px;
    /* border: none; */
    border: 1px solid rgb(48, 210, 8);
    border-radius: 6px;
}